import { Component, OnInit, Input } from '@angular/core';
import { LoginService } from '../login.service'

@Component({
  providers: [LoginService],
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	data: {
		userName:	string, 
		userPwd:	string,
		loginResult:string,
		loginError: string
		};
	@Input() showResult: Boolean;
	@Input() alertType : string;
	constructor(private LoginService: LoginService) { 
		this.data = {userName:'LOGIN', userPwd:'PWD', loginResult:'undefined', loginError:'N/A'};
		this.showResult = true;
		this.alertType = 'alert alert-dismissable'
	}
	getLoginData(userName:string, userPwd:string){
		this.showResult = true;
		this.data.userName = userName;
		this.data.userPwd = userPwd;
		this.data.loginResult = 'Pending server response';
		this.data.loginError = 'N/A';
		this.LoginService.sendData(this.data).subscribe(
			(_res) => {
				this.data = _res;				
			},
			(error) => { 
				this.data.loginError = 'Connection error, no server response'
				this.showResult = false;
				this.alertType = 'alert alert-danger alert-dismissable'
			},
			() => {
				if(this.data.loginResult =='LOGIN_OK') this.alertType = 'alert alert-success alert-dismissable'
				else this.alertType = 'alert alert-danger alert-dismissable';
				this.showResult = false;
			});
	}
	ngOnInit() {}
}



